import React, { useEffect, useRef, useState } from "react";
import axios from "axios";
import { useParams } from "react-router-dom";
import { toast, ToastContainer } from "react-toastify";

const CategoryDetailsLayer = () => {
  const { id } = useParams(); // categoryId
  const [services, setServices] = useState([]);
  const [loading, setLoading] = useState(false);
  const [error, setError] = useState("");
  const [categoryName, setcategoryName] = useState("");
  const [showEditModal, setShowEditModal] = useState(false);
  const [editService, setEditService] = useState(null);
  const [editedServiceName, setEditedServiceName] = useState("");

  const [showDeleteModal, setShowDeleteModal] = useState(false);
  const [deleteServiceId, setDeleteServiceId] = useState(null);

  // Add service modal state
  const [showAddModal, setShowAddModal] = useState(false);
  const [newServiceName, setNewServiceName] = useState("");

  // Pagination state
  const [currentPage, setCurrentPage] = useState(1);
  const servicesPerPage = 10;

  // Search state
  const [searchTerm, setSearchTerm] = useState("");
  const [debouncedTerm, setDebouncedTerm] = useState("");

  // Function to fetch all services
  const fetchAllServices = async () => {
    setLoading(true);
    try {
      const response = await axios.get(
        `${process.env.REACT_APP_BACKEND_URL}/api/categoryService/${id}`
      );
      setServices(response.data.services || []);
      setError("");
    } catch (err) {
      setError(err.response?.data?.message || "Failed to load services.");
    }
    setLoading(false);
  };

  // Fetch services based on categoryId
  useEffect(() => {
    if (id) fetchAllServices();
  }, [id]);

  useEffect(() => {
    const fetchCategory = async () => {
      try {
        const res = await axios.get(
          `${process.env.REACT_APP_BACKEND_URL}/api/category/${id}`
        );

        setcategoryName(res.data.category_name);
      } catch (error) {
        console.error(
          error.response.data?.message || "Failed to load category details"
        );
      }
    };
    if (id) fetchCategory();
  }, [id]);

  // Pagination logic
  const indexOfLastService = currentPage * servicesPerPage;
  const indexOfFirstService = indexOfLastService - servicesPerPage;
  const currentServices = services.slice(
    indexOfFirstService,
    indexOfLastService
  );
  const totalPages = Math.ceil(services.length / servicesPerPage);

  const handlePageChange = (pageNumber) => {
    setCurrentPage(pageNumber);
  };

  const openEditModal = (service) => {
    setEditService(service);
    setEditedServiceName(service.serviceName);
    setShowEditModal(true);
  };

  const handleEditSave = async () => {
    try {
      await axios.put(
        `${process.env.REACT_APP_BACKEND_URL}/api/categoryService/${editService._id}`,
        {
          serviceName: editedServiceName,
        }
      );

      const updated = services.map((s) =>
        s._id === editService._id ? { ...s, serviceName: editedServiceName } : s
      );
      setServices(updated);
      toast.success("Service updated successfully");
      setShowEditModal(false);
    } catch (err) {
      toast.error(err?.response?.data?.error || "Failed to update service");
    }
  };

  const openDeleteModal = (id) => {
    setDeleteServiceId(id);
    setShowDeleteModal(true);
  };

  const confirmDelete = async () => {
    try {
      await axios.delete(
        `${process.env.REACT_APP_BACKEND_URL}/api/categoryService/${deleteServiceId}`
      );
      setServices(services.filter((s) => s._id !== deleteServiceId));
      setShowDeleteModal(false);
      toast.success("Service deleted successfully");
    } catch (err) {
      toast.error("Failed to delete service");
    }
  };

  const handleAddService = async () => {
    const adminToken = localStorage.getItem("adminToken");

    if (!newServiceName.trim()) {
      toast.error("Service name cannot be empty");
      return;
    }

    try {
      await axios.post(
        `${process.env.REACT_APP_BACKEND_URL}/api/categoryService/createService`,
        {
          serviceCategory: id,
          serviceName: newServiceName,
        },
        {
          headers: { Authorization: `Bearer ${adminToken}` },
        }
      );

      toast.success("Service created successfully");
      setShowAddModal(false);
      setNewServiceName("");
      fetchAllServices();
    } catch (err) {
      const backendMessage =
        err.response?.status === 409
          ? err.response?.data?.error
          : err.response?.data?.message;

      toast.error(backendMessage || "Failed to create service");
    }
  };

  // Debounce search term
  useEffect(() => {
    const handler = setTimeout(() => {
      setDebouncedTerm(searchTerm);
    }, 300);

    return () => clearTimeout(handler);
  }, [searchTerm]);

  // Handle search
  useEffect(() => {
    if (!debouncedTerm.trim()) {
      if (id) fetchAllServices();
      return;
    }

    const fetchServices = async () => {
      try {
        const response = await axios.get(
          `${process.env.REACT_APP_BACKEND_URL}/api/categoryService/services/search`,
          { params: { serviceName: debouncedTerm, categoryId: id } }
        );
        setServices(response.data.services || []);
        setCurrentPage(1);
      } catch (err) {
        console.error(err);
        setServices([]);
      }
    };

    fetchServices();
  }, [debouncedTerm, id]);

       const handlePageChangeWithScroll = (page) => {
  handlePageChange(page); // your existing page change logic
  window.scrollTo({ top: 0, behavior: "smooth" }); // scroll to top smoothly
};

  const renderPagination = () => {
    const pages = [];
    const maxVisible = 4; // ðŸ‘ˆ only show 4 pages at a time
    let startPage = Math.max(1, currentPage - Math.floor(maxVisible / 2));
    let endPage = startPage + maxVisible - 1;

    if (endPage > totalPages) {
      endPage = totalPages;
      startPage = Math.max(1, endPage - maxVisible + 1);
    }

        pages.push(
    <li
      key="first"
      className={`page-item ${currentPage === 1 ? " disabled" : ""}`}
    >
      <button
        className="page-link"
        onClick={() => currentPage !== 1 && handlePageChangeWithScroll(1)}
       
      >
        First
      </button>
    </li>
  );

    // Prev Button
    pages.push(
      <li
        key="prev"
        className={`page-item ${currentPage === 1 ? "disabled" : ""}`}
      >
        <button
          className="page-link"
          onClick={() => currentPage > 1 && handlePageChangeWithScroll(currentPage - 1)}
        >
          Prev
        </button>
      </li>
    );

    // Page Numbers
    for (let i = startPage; i <= endPage; i++) {
      pages.push(
        <li
          key={i}
          className={`page-item ${currentPage === i ? "active" : ""}`}
        >
          <button className="page-link" onClick={() => handlePageChangeWithScroll(i)}>
            {i}
          </button>
        </li>
      );
    }

    // Next Button
    pages.push(
      <li
        key="next"
        className={`page-item ${currentPage === totalPages ? "disabled" : ""}`}
      >
        <button
          className="page-link"
          onClick={() =>
            currentPage < totalPages && handlePageChangeWithScroll(currentPage + 1)
          }
        >
          Next
        </button>
      </li>
    );

        pages.push(
      <li
        key="last"
        className={`page-item ${currentPage === totalPages ? "disabled" : ""}`}
      >
        <button
          className="page-link"
          onClick={() => handlePageChangeWithScroll(totalPages)}
        >
          Last
        </button>
      </li>
    );


    return pages;
  };

  const modalRef = useRef(null);

  useEffect(() => {
    // Handle click outside
    const handleClickOutside = (event) => {
      if (
        modalRef.current &&
        !modalRef.current.contains(event.target) &&
        showEditModal
      ) {
        showEditModal(false);
      }
    };

    document.addEventListener("mousedown", handleClickOutside);

    // ðŸ”‘ Scroll lock logic
    if (showEditModal) {
      document.body.style.overflow = "hidden"; // Disable background scroll
    } else {
      document.body.style.overflow = "auto"; // Enable background scroll
    }

    return () => {
      document.removeEventListener("mousedown", handleClickOutside);
      document.body.style.overflow = "auto"; // Cleanup when component unmounts
    };
  }, [showEditModal]);

  useEffect(() => {
    // Handle click outside
    const handleClickOutside = (event) => {
      if (
        modalRef.current &&
        !modalRef.current.contains(event.target) &&
        showDeleteModal
      ) {
        showDeleteModal(false);
      }
    };

    document.addEventListener("mousedown", handleClickOutside);

    // ðŸ”‘ Scroll lock logic
    if (showDeleteModal) {
      document.body.style.overflow = "hidden"; // Disable background scroll
    } else {
      document.body.style.overflow = "auto"; // Enable background scroll
    }

    return () => {
      document.removeEventListener("mousedown", handleClickOutside);
      document.body.style.overflow = "auto"; // Cleanup when component unmounts
    };
  }, [showDeleteModal]);

  return (
    <div className="dashboard-body__content">
      {/* Header & Add Button */}
      <div style={{ display: "flex", justifyContent: "space-between" }}>
        <h4 className="mb-3">Available Services for {categoryName}</h4>
      </div>

      <div className="card common-card border border-gray-five">
        <div className="row align-items-center my-3 mx-3">
          <div className="col-12 col-md-6">
            <input
              type="text"
              className="form-control p-2"
              value={searchTerm}
              onChange={(e) => setSearchTerm(e.target.value)}
              placeholder="Search by Service Name"
            />
          </div>

          <div className="col-12 col-md-6 text-md-end text-center mt-2 mt-md-0">
            <button
              className="btn btn-success "
              onClick={() => setShowAddModal(true)}
            >
              + Add Service
            </button>
          </div>
        </div>

        <div className="card-body">
          {loading ? (
            <p>Loading...</p>
          ) : error ? (
            <p className="text-danger">{error}</p>
          ) : services.length > 0 ? (
            <>
              <div className="table-responsive" style={{ marginTop: "-28px" }}>
                <table className="table text-body">
                  <thead>
                    <tr>
                      <th>SN.</th>
                      <th>Service Name</th>
                      <th>Action</th>
                    </tr>
                  </thead>
                  <tbody>
                    {currentServices.map((service, index) => (
                      <tr key={service._id}>
                        <td>{indexOfFirstService + index + 1}</td>
                        <td>{service.serviceName}</td>
                        <td>
                          <button
                            style={{
                              backgroundColor: "#007bff",
                              color: "white",
                              border: "none",
                              borderRadius: "4px",
                              padding: "5px 10px",
                              marginRight: "5px",
                            }}
                            onClick={() => openEditModal(service)}
                          >
                            Edit
                          </button>
                          <button
                            style={{
                              backgroundColor: "red",
                              color: "white",
                              border: "none",
                              borderRadius: "4px",
                              padding: "5px 10px",
                            }}
                            onClick={() => openDeleteModal(service._id)}
                          >
                            Delete
                          </button>
                        </td>
                      </tr>
                    ))}
                  </tbody>
                </table>
              </div>

              {/* Pagination */}
              <nav aria-label="Service pagination" className="mt-3">
                <ul className="pagination justify-content-center">
                  {renderPagination()}
                </ul>
              </nav>
            </>
          ) : (
            <p className="text-muted">
              No services available for this category.
            </p>
          )}
        </div>
      </div>

      {/* Edit Modal */}
      {showEditModal && (
        <div
          style={{
            position: "fixed",
            top: 0,
            left: 0,
            right: 0,
            bottom: 0,
            backgroundColor: "rgba(0,0,0,0.5)",
            display: "flex",
            alignItems: "center",
            justifyContent: "center",
            zIndex: 1000,
          }}
        >
          <div
            style={{
              backgroundColor: "#fff",
              borderRadius: "10px",
              maxWidth: "400px",
              width: "90%",
              boxShadow: "0 0 10px rgba(0,0,0,0.3)",
              overflow: "hidden",
            }}
          >
            {/* Header */}
            <div
              style={{
                backgroundColor: "#3a3a3a",
                color: "#fff",
                padding: "15px 20px",
                fontSize: "18px",
                fontWeight: "bold",
                display: "flex",
                justifyContent: "space-between",
                alignItems: "center",
              }}
            >
              Edit Service
              <button
                onClick={() => setShowEditModal(false)}
                style={{
                  background: "transparent",
                  border: "none",
                  fontSize: "22px",
                  color: "#fff",
                  cursor: "pointer",
                }}
              >
                &times;
              </button>
            </div>

            {/* Body */}
            <div style={{ padding: "20px" }}>
              <input
                type="text"
                value={editedServiceName}
                onChange={(e) => setEditedServiceName(e.target.value)}
                placeholder="Enter service name"
                style={{
                  width: "100%",
                  padding: "10px",
                  borderRadius: "5px",
                  border: "1px solid #ccc",
                  fontSize: "16px",
                }}
              />
            </div>

            {/* Footer */}
            <div
              style={{
                display: "flex",
                justifyContent: "flex-end",
                padding: "10px 20px 20px",
                gap: "10px",
              }}
            >
              <button
                onClick={() => setShowEditModal(false)}
                style={{
                  backgroundColor: "#6c757d",
                  border: "none",
                  color: "#fff",
                  padding: "8px 15px",
                  borderRadius: "5px",
                  cursor: "pointer",
                }}
              >
                Cancel
              </button>
              <button
                onClick={handleEditSave}
                style={{
                  backgroundColor: "#28a745",
                  border: "none",
                  color: "#fff",
                  padding: "8px 15px",
                  borderRadius: "5px",
                  cursor: "pointer",
                }}
              >
                Save Changes
              </button>
            </div>
          </div>
        </div>
      )}

      {/* Delete Confirmation Modal */}
      {showDeleteModal && (
        <div
          style={{
            position: "fixed",
            top: 0,
            left: 0,
            right: 0,
            bottom: 0,
            backgroundColor: "rgba(0,0,0,0.5)",
            display: "flex",
            alignItems: "center",
            justifyContent: "center",
            zIndex: 1000,
          }}
        >
          <div
            style={{
              backgroundColor: "#fff",
              borderRadius: "10px",
              maxWidth: "400px",
              width: "90%",
              boxShadow: "0 0 10px rgba(0,0,0,0.3)",
              overflow: "hidden",
            }}
          >
            {/* Modal Header */}
            <div
              style={{
                backgroundColor: "#3a3a3a",
                color: "#fff",
                padding: "15px 20px",
                fontSize: "18px",
                fontWeight: "bold",
                display: "flex",
                justifyContent: "space-between",
                alignItems: "center",
              }}
            >
              Confirm Delete
              <button
                onClick={() => setShowDeleteModal(false)}
                style={{
                  background: "transparent",
                  border: "none",
                  fontSize: "22px",
                  color: "#fff",
                  cursor: "pointer",
                }}
              >
                &times;
              </button>
            </div>

            {/* Modal Body */}
            <div
              style={{ padding: "20px", color: "#333", textAlign: "center" }}
            >
              <p>Are you sure you want to delete this service?</p>
            </div>

            {/* Modal Footer */}
            <div
              style={{
                display: "flex",
                justifyContent: "flex-end",
                padding: "10px 20px 20px",
                gap: "10px",
              }}
            >
              <button
                onClick={() => setShowDeleteModal(false)}
                style={{
                  backgroundColor: "#6c757d",
                  border: "none",
                  color: "#fff",
                  padding: "8px 15px",
                  borderRadius: "5px",
                  cursor: "pointer",
                }}
              >
                Cancel
              </button>
              <button
                onClick={confirmDelete}
                style={{
                  backgroundColor: "#dc3545",
                  border: "none",
                  color: "#fff",
                  padding: "8px 15px",
                  borderRadius: "5px",
                  cursor: "pointer",
                }}
              >
                Delete
              </button>
            </div>
          </div>
        </div>
      )}

      {/* Add Service Modal */}
      {showAddModal && (
        <div
          style={{
            position: "fixed",
            top: 0,
            left: 0,
            right: 0,
            bottom: 0,
            backgroundColor: "rgba(0,0,0,0.5)",
            display: "flex",
            alignItems: "center",
            justifyContent: "center",
            zIndex: 1000,
          }}
        >
          <div
            style={{
              backgroundColor: "#fff",
              borderRadius: "10px",
              maxWidth: "400px",
              width: "90%",
              boxShadow: "0 0 10px rgba(0,0,0,0.3)",
              overflow: "hidden",
            }}
          >
            <div
              style={{
                backgroundColor: "#3a3a3a",
                color: "#fff",
                padding: "15px 20px",
                fontSize: "18px",
                fontWeight: "bold",
                display: "flex",
                justifyContent: "space-between",
                alignItems: "center",
              }}
            >
              Add New Service
              <button
                onClick={() => setShowAddModal(false)}
                style={{
                  background: "transparent",
                  border: "none",
                  fontSize: "22px",
                  color: "#fff",
                  cursor: "pointer",
                }}
              >
                &times;
              </button>
            </div>

            <div style={{ padding: "20px" }}>
              <input
                type="text"
                value={newServiceName}
                onChange={(e) => setNewServiceName(e.target.value)}
                placeholder="Enter service name"
                style={{
                  width: "100%",
                  padding: "10px",
                  borderRadius: "5px",
                  border: "1px solid #ccc",
                  fontSize: "16px",
                }}
              />
            </div>

            <div
              style={{
                display: "flex",
                justifyContent: "flex-end",
                padding: "10px 20px 20px",
                gap: "10px",
              }}
            >
              <button
                onClick={() => setShowAddModal(false)}
                style={{
                  backgroundColor: "#6c757d",
                  border: "none",
                  color: "#fff",
                  padding: "8px 15px",
                  borderRadius: "5px",
                  cursor: "pointer",
                }}
              >
                Cancel
              </button>
              <button
                onClick={handleAddService}
                style={{
                  backgroundColor: "#28a745",
                  border: "none",
                  color: "#fff",
                  padding: "8px 15px",
                  borderRadius: "5px",
                  cursor: "pointer",
                }}
              >
                Add Service
              </button>
            </div>
          </div>
        </div>
      )}

      <ToastContainer position="top-right" autoClose={3000} />
    </div>
  );
};

export default CategoryDetailsLayer;
